.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, -50%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, -20%);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 10%;
		width: 70%;
		transform: translate(-50%, 20%);
		@include min-md {
			width: 60%;
		}
		@include min-lg {
			width: 50%;
			transform: translate(-50%, 50%);
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 20%;
			width: 120px;
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 70%;
		transform: translate(30%, 30%);
		@include min-md {
			width: 60%;
			transform: translate(30%, 20%);
		}
		@include min-lg {
			width: 50%;
			transform: translate(30%, 50%);
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(40%, 0) scaleX(-1);
			@include min-md {
				left: 40%;
				width: 140px;
				transform: translate(-50%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 5rem 1.5rem;
				min-height: 80%;
				background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.8), transparent 90%);
				box-shadow: 0 0 2rem rgba(255, 255, 255, 0.3);
				border-radius: 50%;
				width: 40%;
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 100%;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					color: var(--color-dark);
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.title {
						margin: 1rem 0;
					}
					.cover-event {
						h6,
						p,
						span {
							margin-bottom: 0;
						}
						h6,
						span {
							font-weight: 700;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
				position: relative;
				margin-top: 1rem;
			}
		}
	}
}
